html {
  @include font(10px, 10px, 400);

  @media (max-width: $desktop) {
    @include font(10px, 10px, 400);
  }

  @media (max-width: $laptop) {
    @include font(8px, 8px, 400);
  }

  @media (max-width: $tablet) {
    @include font(10px, 10px, 400);
  }
}

body {
  margin: 0;
  padding: 0;
  position: relative;
  min-width: 320px;
  margin: 0 auto;
  font-family: $gotham;
  background-color: $black;
  color: $white;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  padding-top: 9.2rem;

  @media (max-width: $laptop) {
    // шаблон медиа-запроса desktop-first
  }

  @media (min-resolution: $extra) {
    // шаблон для фонового изображения на экранах с повышенной плотностью пикселей
  }


}

button {
  font-family: $gotham;
}

body,
*,
*::before,
*::after {
  box-sizing: border-box;
}

main {
  position: relative;
  flex-grow: 1;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.grecaptcha-badge {
  opacity: 0;
  pointer-events: none;
}


.justify-align-center {
  @media (max-width: $mobile) {
    justify-content: center;
  }
}

.align-center {
  align-items: center;
}

.sport-mos-link {
   display: flex;
   height: 4.8rem;
   padding: 0 1.6rem;
   justify-content: center;
   align-items: center;
   border-radius: 1.2rem;
   background: $pink400;
}

.header-l {
   color: $white;
   text-align: center;
   font-family: $mossport;
   font-size: 6.4rem;
   font-style: normal;
   font-weight: 400;
   line-height: normal;
   text-transform: uppercase;
}
